.about {
  color: var(--c-black);
  padding: 5rem;
}

.about-name {
  font-family: var(--f-1title);
  font-size: var(--f-u30);
}

.about-title {
  font-family: var(--f-1title);
  font-size: var(--f-umax);
  font-weight: bold;
}

.about-more {
  padding-top: 3rem;
}

.about-more > a {
  font-family: var(--f-thin);
  font-size: var(--f-u25);
  color: var(--c-black);
  text-decoration: none;
  background-color: var(--c-brown);
  padding: 10px 30px;
  border-radius: 2px;
}

.about-more > a:hover {
  /* color: var(--c-grey); */
  color: var(--c-darkbrown);
  border: 1px solid var(--c-darkbrown);
  background-color: var(--c-beige);
}

@media screen and (max-width: 600px) {
  .about-more {
    font-size: var(--f-u30);
  }

  .about-name {
    font-size: var(--f-40);
  }

  .about-title {
    font-size: var(--f-u40);
  }

  .about {
    padding: 7rem 3rem 4rem 3rem;
  }

  .about-more > a {
    font-size: var(--f-u15);
    padding: 8px 16px;
  }
}

/* dark mode */

@media (prefers-color-scheme: dark) {
  .about {
    color: var(--c-beige);
  }

  .about-more > a {
    border: 1px solid var(--c-beige);
    background-color: var(--c-darkbrown);
    color: var(--c-beige);
  }

  .about-more > a:hover {
    color: var(--c-darkbrown);
    border: 1px solid var(--c-darkbrown);
    background-color: var(--c-beige);
  }
}
